.mobile_nav::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 0;
    width: 100%;
    border-bottom: 3px solid white;
}

.mobile_nav:hover::before {
    border-bottom: 3px solid rgb(59 130 246);
}

.mobile_nav::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 0;
    width: 100%;
    border-bottom: 1px solid white;
}

.mobile_nav:hover::after {
    transition: border 0.05s ease-in;
    border-bottom: 3px solid rgb(59 130 246);
}

.mobile_nav{
    position: relative;
}

/**---------------------------------------------*/

.sideNav {
    --position: -17rem;
    right: var(--position);
    box-shadow: -0.5rem 0 rgba(0, 0, 0, 0.2)
}

.sideNavTop {
    background-color: #313746;
}

.sideNavBottom {
    background-color: #1e2430;
}

.navToggle {
    right: 0;
    transition: right 0.5s ease-out;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6);
    /*pointer-events: none;*/
    /*display: block;*/
}