.card {
    background-color: white;
    overflow: hidden;
    position: relative;
    display: grid;
    align-items: center;
    justify-items: center;
}

.img {
    --percent: 70%;
    content: '';
    display: block;
    width: var(--percent);
    height: var(--percent);
    transition: all .3s ease-in-out;
    background-size: cover;
}

.img:hover {
    transform: scale(1.1);
}